@charset "utf-8";

@import "bootstrap/bootstrap";
@import "variables.less";
@import "util.less";

// layout
@import "nav.less";
@import "main.less";

// styles
@import "bbcode.less";
@import "wiki.less";
@import "vault.less";
@import "competition.less";
@import "select2.less";
@import "diffview.less";
@import "slider.less";
@import "shoutbox.less";
@import "user.less";
@import "polls.less";

@btn-font-weight: 300;
@font-family-sans-serif: "Roboto", Helvetica, Arial, sans-serif;

body, label, .checkbox label {
	// font-weight: 300;
}

h1,h2,h3 {
  .btn {
    float: right;
    margin: 0 2px;
  }
}

.container, .alert, .well, .panel-body {
  & > h1, & > h2, & > h3, & > h4, & > h5, & > h6 {
    &:first-child {
      margin-top: 10px;
    }
  }
}
.alert-default {
  .alert-variant(@well-bg; @well-border; @text-color);
}

.inactive {
  font-style: italic;
  opacity: 0.5;
}

ul.inline-bullet {
  list-style: none;
  padding: 0;
  margin: 0;

  li {
    display: inline-block;

    &::before {
      content: "\2022";
      padding: 0 2px;
    }

    &:first-child::before {
      content: '';
    }
  }
}

textarea.form-control {
  height: 350px;
  &.gargantuan { height: 1200px; }
  &.massive { height: 800px; }
  &.huge { height: 600px; }
  &.medium { height: 250px; }
  &.small { height: 150px; }
  &.tiny { height: 80px; }
}

.dl-horizontal.dl-wide {
  dt { width: 240px; }
  dd { margin-left: 260px; }
}

.option-panel {
  .panel-heading {
    span {
      padding: 0 5px;
    }
    label {
      border: 1px solid @gray-light;
      border-radius: 4px;
      padding: 1px 6px 1px 26px;

      &:hover {
        background-color: @gray-lighter;
      }
    }
  }
}

.btn-minimal {
  .btn-link;
  color: @btn-default-color;

  &, &:active, &.active, &[disabled], fieldset[disabled] & {
    background-color: #f6f6f6;
  }

  &:hover, &:focus {
    text-decoration: none;
    background-color: #dedede;
  }
}

.btn-xxs {
  .button-size(2px; 3px; 10px; 1; 10px);
}