.slider {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 500px;

  .loading {
    position: absolute;
    top: 0;
    left: 0;
    background: none repeat scroll 0 0 rgba(0, 0, 0, 0.3);
    color: white;
    height: 420px;
    line-height: 420px;
    text-align: center;
    z-index: 100;
  }

  .slides {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 420px;
    overflow: hidden;
  }

  .thumbs {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 80px;

    .p {
      position: absolute;
      top: 0;
      left: 0;
      width: 72px;
      height: 78px;
      border-top: 4px solid @gray-light;
      opacity: 0.7;

      &:hover {
        border-color: @gray-darker;
        opacity: 1;
      }

      &.pav {
        border-color: @brand-primary;
        opacity: 1;
      }

      .i {
        position: absolute;
        top: 2px;
        left: 0px;
        max-width: 72px;
        max-height: 72px;
      }
    }
  }

  .arrow {
    display: block;
    position: absolute;
    width: 40px;
    height: 40px;
    cursor: pointer;
    overflow: hidden;
    background-color: #dedede;

    &.left { left: 8px; }
    &.right { right: 8px; }
  }
}
